export interface AppError {
  code: string;
  message: string;
  details?: any;
  timestamp: string;
  jobId?: string;
  retryable?: boolean;
  category?: ErrorCategory;
  severity?: ErrorSeverity;
  suggestion?: string;
}

type ErrorCategory =
  | 'validation'
  | 'network'
  | 'database'
  | 'security'
  | 'file'
  | 'rate-limit'
  | 'cost'
  | 'system';

type ErrorSeverity = 'critical' | 'error' | 'warning' | 'info';

interface ErrorDisplayProps {
  errors?: AppError[];
  onRetry?: (error: AppError) => void;
  onClear?: () => void;
  showToast?: boolean;
  compact?: boolean;
}

