export function ErrorDisplay({
  errors = [],
  onRetry,
  onClear,
  showToast = true,
  compact = false,
}: ErrorDisplayProps) {
  const [expandedErrors, setExpandedErrors] = useState<Set<string>>(new Set());
  const [retryingErrors, setRetryingErrors] = useState<Set<string>>(new Set());

  /**
   * „Ç®„É©„Éº„Çí„Éà„Éº„Çπ„Éà„ÅßË°®Á§∫
   */
  useEffect(() => {
    if (showToast && errors.length > 0) {
      const latestError = errors[errors.length - 1];
      const errorInfo = ERROR_CODES[latestError.code] || {};

      const toastType =
        errorInfo.severity === 'critical' ? 'error' :
        errorInfo.severity === 'warning' ? 'warning' :
        errorInfo.severity === 'info' ? 'info' :
        'error';

      toast[toastType](latestError.message, {
        description: errorInfo.suggestion,
        action: latestError.retryable && onRetry ? {
          label: 'ÂÜçË©¶Ë°å',
          onClick: () => handleRetry(latestError),
        } : undefined,
      });
    }
  }, [errors, showToast]);

  /**
   * „Ç®„É©„ÉºÂÜçË©¶Ë°å
   */
  const handleRetry = async (error: AppError) => {
    setRetryingErrors((prev) => new Set(prev).add(error.code));

    try {
      if (error.jobId) {
        // „Ç∏„Éß„ÉñÂÜçË©¶Ë°å
        await apiClient.post(`/jobs/${error.jobId}/retry`);
      }
      onRetry?.(error);
    } catch (retryError) {
      toast.error('ÂÜçË©¶Ë°å„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
    } finally {
      setRetryingErrors((prev) => {
        const newSet = new Set(prev);
        newSet.delete(error.code);
        return newSet;
      });
    }
  };

  /**
   * „Ç®„É©„ÉºÂ±ïÈñã„Éà„Ç∞„É´
   */
  const toggleExpanded = (errorCode: string) => {
    setExpandedErrors((prev) => {
      const newSet = new Set(prev);
      if (newSet.has(errorCode)) {
        newSet.delete(errorCode);
      } else {
        newSet.add(errorCode);
      }
      return newSet;
    });
  };

  /**
   * „Ç´„ÉÜ„Ç¥„É™„Ç¢„Ç§„Ç≥„É≥ÂèñÂæó
   */
  const getCategoryIcon = (category?: ErrorCategory) => {
    switch (category) {
      case 'validation':
        return <AlertCircle className="h-4 w-4" />;
      case 'network':
        return <Network className="h-4 w-4" />;
      case 'database':
        return <Database className="h-4 w-4" />;
      case 'security':
        return <Shield className="h-4 w-4" />;
      case 'file':
        return <FileWarning className="h-4 w-4" />;
      case 'rate-limit':
        return <Clock className="h-4 w-4" />;
      case 'cost':
        return <DollarSign className="h-4 w-4" />;
      case 'system':
        return <Bug className="h-4 w-4" />;
      default:
        return <AlertCircle className="h-4 w-4" />;
    }
  };

  /**
   * ÈáçË¶ÅÂ∫¶„Å´„Çà„ÇãËâ≤ÂèñÂæó
   */
  const getSeverityColor = (severity?: ErrorSeverity) => {
    switch (severity) {
      case 'critical':
        return 'text-red-600 dark:text-red-400';
      case 'error':
        return 'text-destructive';
      case 'warning':
        return 'text-yellow-600 dark:text-yellow-400';
      case 'info':
        return 'text-blue-600 dark:text-blue-400';
      default:
        return 'text-destructive';
    }
  };

  /**
   * ÈáçË¶ÅÂ∫¶„Å´„Çà„Çã„Éê„É™„Ç¢„É≥„ÉàÂèñÂæó
   */
  const getSeverityVariant = (severity?: ErrorSeverity): "default" | "destructive" | "outline" | "secondary" => {
    switch (severity) {
      case 'critical':
      case 'error':
        return 'destructive';
      case 'warning':
        return 'secondary';
      case 'info':
        return 'outline';
      default:
        return 'destructive';
    }
  };

  if (errors.length === 0) {
    return null;
  }

  /**
   * „Ç≥„É≥„Éë„ÇØ„ÉàË°®Á§∫
   */
  if (compact) {
    return (
      <div className="space-y-2">
        {errors.map((error, index) => {
          const errorInfo = ERROR_CODES[error.code] || {};
          const isExpanded = expandedErrors.has(error.code);
          const isRetrying = retryingErrors.has(error.code);

          return (
            <Alert
              key={`${error.code}-${index}`}
              variant={getSeverityVariant(errorInfo.severity)}
            >
              <div className="flex items-start gap-2">
                {getCategoryIcon(errorInfo.category)}
                <div className="flex-1">
                  <AlertDescription className="font-medium">
                    {error.message}
                  </AlertDescription>
                  {errorInfo.suggestion && (
                    <p className="text-sm text-muted-foreground mt-1">
                      {errorInfo.suggestion}
                    </p>
                  )}
                </div>
                {error.retryable && onRetry && (
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => handleRetry(error)}
                    disabled={isRetrying}
                  >
                    <RefreshCw className={`h-3 w-3 ${isRetrying ? 'animate-spin' : ''}`} />
                  </Button>
                )}
              </div>
            </Alert>
          );
        })}
      </div>
    );
  }

  /**
   * Ë©≥Á¥∞Ë°®Á§∫
   */
  return (
    <Card>
      <CardHeader>
        <div className="flex items-center justify-between">
          <div>
            <CardTitle className="flex items-center gap-2">
              <AlertCircle className="h-5 w-5" />
              „Ç®„É©„ÉºÊÉÖÂ†±
            </CardTitle>
            <CardDescription>
              {errors.length}‰ª∂„ÅÆ„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Å¶„ÅÑ„Åæ„Åô
            </CardDescription>
          </div>
          {onClear && (
            <Button variant="outline" size="sm" onClick={onClear}>
              „ÇØ„É™„Ç¢
            </Button>
          )}
        </div>
      </CardHeader>
      <CardContent>
        <ScrollArea className="h-96">
          <div className="space-y-3">
            {errors.map((error, index) => {
              const errorInfo = ERROR_CODES[error.code] || {};
              const isExpanded = expandedErrors.has(error.code);
              const isRetrying = retryingErrors.has(error.code);

              return (
                <div
                  key={`${error.code}-${index}`}
                  className="border rounded-lg p-3 space-y-2"
                >
                  <div className="flex items-start justify-between">
                    <div className="flex items-start gap-2">
                      <div className={getSeverityColor(errorInfo.severity)}>
                        {getCategoryIcon(errorInfo.category)}
                      </div>
                      <div className="space-y-1">
                        <div className="flex items-center gap-2">
                          <span className="font-medium">{error.message}</span>
                          <Badge variant="outline" className="text-xs">
                            {error.code}
                          </Badge>
                        </div>
                        {errorInfo.suggestion && (
                          <p className="text-sm text-muted-foreground">
                            üí° {errorInfo.suggestion}
                          </p>
                        )}
                        <div className="flex items-center gap-3 text-xs text-muted-foreground">
                          <span>{new Date(error.timestamp).toLocaleTimeString('ja-JP')}</span>
                          {error.jobId && (
                            <span>Job: {error.jobId}</span>
                          )}
                        </div>
                      </div>
                    </div>
                    <div className="flex items-center gap-1">
                      {error.retryable && onRetry && (
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => handleRetry(error)}
                          disabled={isRetrying}
                        >
                          <RefreshCw className={`h-4 w-4 ${isRetrying ? 'animate-spin' : ''}`} />
                        </Button>
                      )}
                      {error.details && (
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => toggleExpanded(error.code)}
                        >
                          {isExpanded ? (
                            <ChevronDown className="h-4 w-4" />
                          ) : (
                            <ChevronRight className="h-4 w-4" />
                          )}
                        </Button>
                      )}
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={() => navigator.clipboard.writeText(
                          `Error: ${error.code}\nMessage: ${error.message}\nTime: ${error.timestamp}`
                        )}
                      >
                        <Copy className="h-3 w-3" />
                      </Button>
                    </div>
                  </div>

                  {/* Ë©≥Á¥∞ÊÉÖÂ†± */}
                  {error.details && isExpanded && (
                    <div className="mt-2 p-2 bg-muted rounded text-xs font-mono">
                      <pre>{JSON.stringify(error.details, null, 2)}</pre>
                    </div>
                  )}
                </div>
              );
            })}
          </div>
        </ScrollArea>
      </CardContent>
    </Card>
  );
}